<template>
  <div class="col-sm-3 col-md-3 col-lg-3 ml-auto mr-3 pb-2 card">
    <form class="form-inline" id="formConsultaCPF">
			<label for="cpfConsulta" class="bmd-label-floating text-light" id="labelConsultaCPF">
        <h5>Consulte <strong style="color: #41698E;">gratuitamente</strong> o Score do seu <strong>CPF</strong></h5>
      </label>
      <!-- <the-mask class="form-control" id="campoConsulta" v-model="dado" name="dado" :mask="['###.###.###-##', '##.###.###/####-##']" required/> -->
      <the-mask class="form-control" id="campoConsulta" v-model="cpfConsulta" name="cpfConsulta" :mask="['###.###.###-##']" required/>
      <router-link id="consultarScore" to="/login">
        <button-component tipo="submit" rotulo="Consultar" classe="btn btn-default" @click.native="validarDado(cpfConsulta, $event)"></button-component>
      </router-link>
      <span class="invalido text-danger">{{ msgValidacao }}</span>
    </form>
  </div>
</template>

<script>
  import ButtonComponent from '../../shared/ButtonComponent.vue'
  import CPF from 'gerador-validador-cpf'
  export default {
    name: 'form-consulta-cpf-component',
    components: {
      ButtonComponent,
    },
    data(){
      return{
        cpfConsulta: null,
        msgValidacao: ''
      }
    },
    methods: {
      validarDado(cpfConsulta, event){
        if (!CPF.validate(cpfConsulta)){
          event.preventDefault()
          this.msgValidacao = 'Insira um CPF válido'
        }
      }
    }
  }
</script>

<style lang="scss">
  .card{
    #formConsultaCPF{
      #labelConsultaCPF{
        width: 100%;
        color: #213345!important;
        margin-top: 1em;

        h5{
          line-height: 1.5;
        }
      }
      #campoConsulta{
        width: 65%;
        &:valid {
          background-image: linear-gradient(0deg,#41698E 2px,rgba(213,0,0,0) 0),linear-gradient(0deg,rgba(0,0,0,.26) 1px,transparent 0);
        }
      }
      #consultarScore{
        color: #ffffff;
        text-decoration: none;
        width: max-content;
        margin-left: auto;
      }
      #msgValidacao{
        padding-top: 0.5em;
        font-weight: bold;
      }
    }
  }
</style>
