<template>
  <g-signin-button class="googleBtn"
    :params="googleSignInParams"
    @success="onSignInSuccess"
    @error="onSignInError">
    <i class="fab fa-google"></i>
  </g-signin-button>
</template>
<script>
export default {
  name:'google-login',
  created(){
    let ckeditor = document.createElement('script');
    ckeditor.setAttribute('src',"https://apis.google.com/js/api:client.js");
    document.head.appendChild(ckeditor);
  },
  data () {
    return {
      /**
       * The Auth2 parameters, as seen on
       * https://developers.google.com/identity/sign-in/web/reference#gapiauth2initparams.
       * As the very least, a valid client_id must present.
       * @type {Object}
       */
      googleSignInParams: {
        client_id: '720524536347-e89s6n7qnr6hdrsij86jvbs5upeno7m5.apps.googleusercontent.com'
      }
    }
  },
  methods: {
    onSignInSuccess (googleUser) {
      // `googleUser` is the GoogleUser object that represents the just-signed-in user.
      // See https://developers.google.com/identity/sign-in/web/reference#users
      var google = googleUser.getBasicProfile() // etc etc
      var data = {
        email: google.U3, // para testar 10845393901 senha
      }
      console.log(google);
      this.$http.post('https://bankscore.com.br/rest/api/logarConsumidorSocial', data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        console.log(response);
        if (response.data.user === null) { // login com sucesso
          let profile = {
            "tipoRedeSocial":"1",
            "imagemUrl":google.Paa,
            "googleId":google.Eea,
            "email":google.U3,
            "nome":google.ig
          }
          sessionStorage.setItem('RedeSocial', JSON.stringify(profile))
          this.$router.push('/register') // redireciona o usuário pra página o cadastro
        } else{ // login não existe
          sessionStorage.setItem('usuario', JSON.stringify(response.data.user))
          sessionStorage.setItem('token', response.data.token)
          this.$router.push('/perfil') // redireciona o usuário pra página principal
        }
      })
      .catch(e => {
        alert(e)
      })
      // this.$router.push('/register')
    },
    onSignInError (error) {
      // `error` contains any error occurred.
      console.log('OH NOES', error)
    }
  }
}
</script>

<style>
.googleBtn{
  background-color: #e64522;
  display: inline-block;
  padding: 4px 8px;
  border-radius: 3px;
  color: #e64522;
  &:hover{
    color: #e64522!important;
    background: #fff;
    border: 1.5px solid #e64522;
  }
}
</style>
