<template>
  <div :class="classediv">
    <input @change="$emit('change', valor)" @input="$emit('input', valor)" :value="valor" :type="type" :name="name" :id="id" :peso="peso" required/>
    <label @click="emitir" :for="labelradio">{{ label }}</label>
  </div>
</template>

<script>
  export default {
    name: 'radio-component',
    props: {
      valor: null, classediv: String, labelradio: String, label: String, type: String, name: String, id: String, peso: Number
    },
    methods:
    {
      emitir: function (){
        this.$emit('click', this.valor)
        this.$emit('finished', this.peso, this.valor, this.valor)
      }
    }
  }
</script>

<style lang="scss">
  .perguntas{
    width: 16.5vw;
    margin-bottom: 1vh;
    height: 5vh;
    input[type=radio] {
      display: none;
      &+label{
        width: 90%;
        font-size: 0.9rem;
        line-height: 5vh;
        color: #ffffff;
        text-align: center;
        vertical-align: middle;
        cursor: pointer;
        background-color: #ec9100d2;
        border: 1px solid #ffffff;
        border-radius: 3px;
        text-shadow: 0 1px 1px rgba(0,0,0,.4);
      }
      &:checked + label {
        border: 2px solid #213345ad;
      }
    }
  }
</style>
