<template>
  <form class="form-row mb-0" @submit="alterarCadastro">
    <div class="form-group col-md-12 col-lg-12">
      <label class="control-label" for="cpf">CPF</label>
      <the-mask class="form-control" v-model="form.cpf" :mask="['###.###.###-##']" disabled/>
      <span class="invalido text-warning">{{ msgValidacao }}</span>
    </div>
    <input-component classediv="form-group col-md-12 col-lg-12" labelinput="nome" label="Nome" classeinput="form-control"
    tipoinput="text" v-model="form.nome"></input-component>
    <div class="form-group col-md-4 col-lg-4">
      <label class="control-label" for="telefone">Telefone</label>
      <the-mask class="form-control" type="tel" v-model="form.telefone" :mask="['(##)#####-####']" required/>
    </div>
    <input-component classediv="form-group col-md-8 col-lg-8" labelinput="email" label="E-mail" classeinput="form-control"
    tipoinput="email" v-model="form.email"></input-component>
    <input-component classediv="form-group col-md-6 col-lg-6" labelinput="senha" label="Senha" classeinput="form-control"
    tipoinput="password" v-model="form.senha"></input-component>
    <input-component classediv="form-group col-md-6 col-lg-6" labelinput="senhaConfirm" label="Confirmar senha" classeinput="form-control"
    tipoinput="password" v-model="form.senhaConfirm"></input-component>
    <div class="form-group col-md-12 col-lg-12 text-center">
      <button-component tipo="submit" rotulo="Salvar" classe="btn btn-default"></button-component>
    </div>
  </form>
</template>

<script>
  import axios from 'axios';
  import CPF from 'gerador-validador-cpf'
  import HeaderComponent from '../shared/HeaderComponent.vue'
  import InputComponent from '../shared/InputComponent.vue'
  import ButtonComponent from '../shared/ButtonComponent.vue'
  export default {
    name: 'alterar-cadastro-form-component',
    components: {
      HeaderComponent, InputComponent, ButtonComponent
    },
    data(){
      return{
        form: { cpf: null, nome: null, telefone: null, email: null, senha: null, senhaConfirm: null, token: null},
        msgValidacao: '', usuario: false
      }
    },
    created(){
      let usuarioAux = sessionStorage.getItem('usuario') //pega a info de "usuario"
      if (usuarioAux){
        this.usuario = JSON.parse(usuarioAux) //this.usuario recebendo as infos de usuario em forma de objeto
        this.form.cpf = this.usuario.cpf
        this.form.nome = this.usuario.name
        this.form.telefone = this.usuario.phone
        this.form.email = this.usuario.email
      }
    },
    methods: {
      alterarCadastro (event){
        if (this.form.nome && this.form.telefone && this.form.email && this.form.senha && this.form.senhaConfirm) {
          axios.post('http://35.198.23.222/Rest/api/editarUsuarioConsumidor', { //aqui põe a url da API
            cpf: this.form.cpf,
            name: this.form.nome,
            phone: this.form.telefone,
            email: this.form.email,
            password: this.form.senha,
            password_confirmation: this.form.senhaConfirm,
            token: sessionStorage.getItem('token')
          })
          .then(response => {
            console.log(response.data)
            sessionStorage.setItem('usuario', JSON.stringify(response.data.user))
            // if (response.data.status == true){ //cadastro alterado com sucesso
            //   console.log(response.data)
            //   // alert('cadastro alterado com sucesso')
            //   // sessionStorage.setItem('usuario', JSON.stringify(response.data))
            // }
            // else{ //erro ao alterar cadastro
            //   alert(response.data.message)
            // }
          })
          .catch(e => {
            alert(e)
            // this.errors.push(e)
          })
        }else {
          alert('vaai kassinao')
          event.preventDefault()
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .form-row{
    .invalido{
      font-size: 0.9rem;
    }
  }
</style>
