<template>
  <div class="row mr-0 scores">
    <div v-for="(bar, key) in bars" :class="bar.div" :key="bar.index">
      <div v-if="key != 1" class="row">
        <p-component v-if="key == 0" classeDiv="col-md-8 col-lg-8" :classe="bar.classeTexto" :texto="bar.texto"></p-component>
        <div class="col-md-4 col-lg-4">
          <score-card-component ref="scoreC1" estiloprogress="height: 22vh; width: 10vw;" insidespan="progress-value" outsidespan="progress-value-min"
          :estilo="bar.estilo" :score="bar.valor" :bars="bars" :valuenow="bar.valor" progressdiv="mb-3 progress"></score-card-component>
        </div>
        <p-component v-if="key == 2" classeDiv="col-md-8 col-lg-8" :classe="bar.classeTexto" :texto="bar.texto"></p-component>
      </div>
      <score-card-component ref="scoreC2" v-else estiloprogress="height: 32vh; width: 15vw;" insidespan="progress-value" outsidespan="progress-value-max"
      :estilo="bar.estilo" :score="bar.valor" :bars="bars" :valuenow="bar.valor" rotulo="Seu Score" progressdiv="mb-3 ml-0 progress"></score-card-component>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import PComponent from '../shared/PComponent.vue'
  import ScoreCardComponent from '../shared/ScoreCardComponent.vue'
  import ButtonComponent from '../shared/ButtonComponent.vue'
  export default {
    components: {
      PComponent, ScoreCardComponent, ButtonComponent
    },
    name: 'score-component',
    data () {
      return {
        bars: [
          { div: 'col-md-5 col-lg-5 mt-5', valor: 0, estilo: '', classeTexto: 'text-right texto-idade', texto: 'Score médio das pessoas que vivem na mesma cidade que você' },
          { div: 'col-md-2 col-lg-2', valor: 0, estilo: '' },
          { div: 'col-md-5 col-lg-5 mt-5', valor: 0, estilo: '', classeTexto: 'text-left texto-idade', texto: 'Score médio das pessoas com a mesma faixa etária que você' }
        ]
      }
    },
    created () {
      this.usuario = JSON.parse(sessionStorage.getItem('usuario'))
      var data = {
        documento: this.usuario['cpf'],
        tipoPesquisa: 'PF',
        token: sessionStorage.getItem('token')
      }


      this.$http.post('http://35.198.23.222/Rest/api/v1/calculoScoreCidade', data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        if (response.data.status == true) { // login com sucesso
          this.bars[0].valor = response.data.data.score
          this.$refs.scoreC1[0].changeStyle()
        } else if (response.data.status == false) { // login não existe
          this.bars[0].valor = undefined
          this.$refs.scoreC1[0].changeStyle()
        } else {
          alert(response.data)
        }
      })
      .catch(e => {
        // da erro
      })

      this.$http.post('http://35.198.23.222/Rest/api/v1/calculoScore', data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        if (response.data.status == true) { // login com sucesso
          this.bars[1].valor = response.data.data.score
          this.$refs.scoreC2[0].changeStyle()
        } else if (response.data.status == false) { // login não existe
          this.bars[1].valor = undefined
          this.$refs.scoreC2[0].changeStyle()
        } else {
          alert(response.data)
        }
      })
      .catch(e => {
        // da erro
      })

      this.$http.post('http://35.198.23.222/Rest/api/v1/calculoScoreIdade', data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        if (response.data.status == true) { // login com sucesso
          this.bars[2].valor = response.data.data.score
          this.$refs.scoreC1[0].changeStyle()
        } else if (response.data.status == false) { // login não existe
          this.bars[2].valor = undefined
          this.$refs.scoreC1[0].changeStyle()
        } else {
          alert(response.data)
        }
      })
      .catch(e => {
        // da erro
      })
    }
  }
</script>

<style lang="scss">
  .scores{
    margin-top: 1vh;
  }
</style>
